import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

import HomeTitle from '../components/homepage/homeTitle'
import VideoCaption from '../components/videoCaption/videoCaption'

import captionVideo from '../assets/video/404.mp4'

const NotFoundPage = () => (
  <Layout>
    <SEO title="404: Not found" description="A página pretendida não existe. Certifique-se que introduziu o endereço correto." />
    <HomeTitle title="pagina não encontrada." leading="@pathfinderdesigns" />
    <VideoCaption video={captionVideo} caption="A pagina pretendida não existe." />
  </Layout>
)

export default NotFoundPage
